    // 卡片在线或者离线，再考虑


    // import {request} from "../../network/request";
    import CardInDTInPM from "./cardindynamictableinpm/CardInDTInPM";
    import axios from 'axios'
    export default {
        name: "DynamicTableInPM",
        data() {
            return {

                // 这部分是用来给drawer表格的
                table: false,
                titleName: '',
                dialog: false,
                loading: false,
                gridData: [],

                // 这个是用来监听开启collapse的index
                activeCollapseName: ['1'],

                //测点值和报警flag，我会把它写入card里在card初始化时加载 TODO：
                arsa_41301: 0,
                prcsa_4201: 0,
                pisa_4204: 0,

                arsa_41301_flag: 0,
                prcsa_4201_flag: 0,
                pisa_4204_flag: 0,
            }
        },
        components: {
            CardInDTInPM,
        },
        methods: {
          isFlagActive(flag) {
              return flag === 1;
          },


          //用来打开drawer前请求当前在线测点数据，传入参数为drawer本身显示参数，测点所属类别
          requestOnlinePointTableForDrawer(categoryName,tableType) {

              this.titleName = categoryName;
              this.table=true;
              this.gridData.length=0;

              axios.post("http://127.0.0.1:8000/online_point_DT/", {Type:tableType})
                  .then(res=>{
                      // // eslint-disable-next-line no-console
                      // console.log(res.data);
                      // // eslint-disable-next-line no-console
                      // console.log(typeof(res.data));
                      // 这里我已经拿到列表了，将列表显示在表格上
                      for(let key in res.data) {
                          if(res.data.hasOwnProperty(key)) {
                              this.gridData.push({
                                  // 这个地方要加一个选择
                                  PointName: res.data[key][0],
                                  PointCode: res.data[key][1],
                                  category: res.data[key][2],
                                  status: res.data[key][3],
                              })
                          }
                      }
                  }).catch(err=>{
                  // eslint-disable-next-line no-console
                  console.log(err);
              })
          },




        },
        created() {

